<template>
  <div class="slider">
      <div class="slider-items" :style="{ 'margin-left': '-' + (100 * slideIndex) + '%'}">
          <div class="slider-item" v-for="item in carouselData">{{ item.id }}</div>
      </div>
      <div class="slider-button-left" @click="prevSlide"><img src="@/assets/left-arrow.svg" alt=""></div>
      <div class="slider-button-right" @click="nextSlide"><img src="@/assets/right-arrow.svg" alt=""></div>
  </div>
  <div class="slider-indicator">
          <div class="indicator" v-for="i in count">
                <img v-if="i === (slideIndex + 1)" src="@/assets/active-eclipse.svg" alt="">
                <img v-else src="@/assets/eclipse.svg" alt="">
          </div>
    </div>
</template>

<script>
export default {
    name: 'AppSlider',
    props: {
        carouselData: {
            type: Array,
            default: () => []
        }
    },
    data() {
        return {
            slideIndex: 0,
            count: this.carouselData.length
        }
    },
    methods: {
        prevSlide() {
            if (this.slideIndex > 0) {
                this.slideIndex--;
            }
        },
        nextSlide() {
            if (this.slideIndex >= this.carouselData.length - 1) {
                this.slideIndex = 0;
            } else {
                this.slideIndex++;
            }
        }
    }
}
</script>

