<template>
  <div class="list-item" @mouseover="showBtn(item)" @mouseout="removeBtn(item)">
                <img :src="require('@/assets/' + item.img)" alt="" class="item-photo">
                <div class="item-name">{{ item.name }}</div>
                <div class="item-info-line">
                  <div class="item-code">{{ item.code }}</div>
                    <div class="item-city">{{ item.city }}</div>
                    <select name="item-volume" id="" class="item-volume">
                        <option value="">0,33</option>
                        <option value="">0,5</option>
                        <option value="">1</option>
                    </select>  
                </div>
                <div class="item-cp-line">
                    <div class="item-count">
                        <div class="count-minus" @click="countMinus(item)">-</div>
                        <div class="count">{{ item.count }}</div>
                        <div class="count-plus" @click="countPlus(item)">+</div>
                    </div>
                    <div class="item-price">{{ getPrice(item) }} &#8381;</div>
                </div>
                <div class="item-rb-line">
                    <div class="item-rate">
                        <img src="@/assets/active-star.svg" alt="" class="rate-star" v-for="star in item.rate">
                        <img src="@/assets/star.svg" alt="" class="rate-star" v-for="star in 5 - item.rate">
                    </div>
                    <div class="item-to-cart-button" :class="{active: item.btnVisible}">В корзину</div>
                </div>
                
    </div>
</template>

<script>
export default {
    name: 'CardView',
    props: {
        item: {
            type: Object,
            require: true
        }
    },
    methods: {
        countPlus(item) {
            item.count = item.count + 1;
        },
        countMinus(item) {
            if (item.count > 0) {
                item.count = item.count - 1;
            }
        },
        getPrice(item) {
            if (item.count !== 0) {
                return Math.floor(item.count * item.price * 100) / 100;
            } else {
                return item.price
            }
        },
        showBtn(item) {
            item.btnVisible = true;
        },
        removeBtn(item) {
            item.btnVisible = false;
        }
    }
}
</script>

